.header__logo {
    width: 86px;
    height: 35px;
    margin-left: 115px;


    @media (min-width: @tablet-width) {
      width: 113px;
      height: 46px;
      margin-left: 0;
      grid-column: 1/2;
      grid-row: 1/2;
    }
    @media (min-width: @desktop-width) {
    width: 149px;
      height: 72px;
      grid-column: 2/3;
      grid-row: 1/2;
      text-align: center;
    }

    &:hover{
    opacity: 0.6;
    }
    &:active{
    opacity: 0.3;
  }
}
.logo-bdg{
    @media (min-width: @tablet-width) {
        width: 113px;
        height: 46px;
        background-image: url(../img/logo/logo-tablet.svg);
        background-position: center;
    }
    @media (min-width: @desktop-width) {
        width: 149px;
        height: 72px;
        background-image: url(../img/logo/logo-desktop.svg);

    }
}
