}
.button {
  width: 260px;
  box-sizing: border-box;
  color: #444;
  border: 2px solid #444;
  padding: 16px 65px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  background-color: #fff;
  cursor: pointer;
}
.button:hover {
  background-color: #444444;
  color: #fff;
  opacity: 1;
}
.button:active {
  font-size: 17px;
  color: #7C7C7C;
}
