  }
}
.header {
  position: relative;
  width: auto;
  height: 79px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 22px 0;
  background-color: #fff;
  border-bottom: 1px solid #ECECEC;
}
@media (min-width: 768px) {
  .header {
    height: 89px;
    display: grid;
    grid-template-columns: 2fr auto 2fr;
    grid-template-rows: 88px 60px;
    border: none;
    padding: 0 50px;
  }
}
.header__toggle {
  position: absolute;
  top: 32px;
  right: 30px;
  display: block;
  width: 20px;
  height: 14px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}
.header__nav--closed .header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 1px;
  width: 20px;
  height: 2px;
  background-color: black;
  box-shadow: 0 6px 0 0 #000, 0 12px 0 0 #000;
}
.header__nav--closed .header__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.3), 0 12px 0 0 rgba(0, 0, 0, 0.3);
}
.header__nav--opened .header__toggle {
  top: 30px;
  right: 1px;
}
.header__nav--opened .header__toggle::before,
.header__nav--opened .header__toggle::after {
  content: "";
  position: absolute;
  width: 21px;
  height: 2px;
  background-color: #000;
}
.header__nav--opened .header__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header__nav--opened .header__toggle::after {
  transform: rotate(-45deg);
}
.header__nav--opened .header__toggle:active::before,
.header__nav--opened .header__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
